import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import StepButton from "@mui/material/StepButton";

const dispatchedEvents = [];

const SrsMuiStepButton = function (props, ref) {
  return (
    <StepButton
      active={props.active}
      completed={props.completed}
      disabled={props.disabled}
      icon={props.icon}
      optional={props.optional}
      onClick={props.onClick}
      theme={props.muiTheme}
    >
      <slot />
    </StepButton>
  );
};

const styles = `
  /* Custom styles for the MuiStepButton component */
`;

const props = [
  {
    name: "active",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "completed",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "icon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "optional",
    defaultValue: null,
    type: "string",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiStepButton),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
